import { milestonesMock } from '../mocks/milestonesMock';
import { MilestoneModel } from '../models/MilestoneModels';
import { ApiEndpoint, ApiMethod, ApiUrl, environment, Environment } from './constants';

const enabled = false;

const MilestoneServices = {
	getMilestones(): Promise<MilestoneModel[]> {
		if (!enabled || environment === Environment.dev) {
			return Promise.resolve(milestonesMock);
		}
		const url = `${ApiUrl}${ApiEndpoint.MILESTONES}`;
		const requestInit: RequestInit = {
			method: ApiMethod.GET,
		};
		return fetch(url, requestInit).then((res) => res.json());
	},
};

export default MilestoneServices;
