import React, { useMemo } from 'react';
import moment from 'moment';

function getDaysUntilDate(eventDate: string) {
	return moment(eventDate).diff(moment(), 'days');
}

function getWeeksUntilDate(eventDate: string) {
	return moment(eventDate).diff(moment(), 'weeks');
}

function getMonthsUntilDate(eventDate: string) {
	return moment(eventDate).diff(moment(), 'months');
}

const dayEmoji = '‚òÄÔ∏è';
const weekEmoji = 'üí´';
const monthEmoji = 'üåô';

export const Countdown: React.FC<{ title: string; date: string }> = (props) => {
	const countdownDays = useMemo(() => getDaysUntilDate(props.date), [props.date]);
	const countdownWeeks = useMemo(() => getWeeksUntilDate(props.date), [props.date]);
	const countdownMonths = useMemo(() => getMonthsUntilDate(props.date), [props.date]);

	const showDays = countdownDays <= 30;
	const showWeeks = countdownWeeks <= 20;

	return (
		<div>
			<p>{props.title}</p>
			<h4>
				{
					showDays ?
						`${dayEmoji.repeat(countdownDays)} ${countdownDays} days` :
						(showWeeks ?
							`${weekEmoji.repeat(countdownWeeks)} ${countdownWeeks} weeks` :
							`${monthEmoji.repeat(countdownMonths)} ${countdownMonths} months`)
				}
			</h4>
		</div>
	);
};
