import { CountdownModel } from '../models/CountdownModels';
import { ApiEndpoint, ApiMethod, ApiUrl, environment, Environment } from './constants';
import { countdownsMock } from '../mocks/countdownsMock';

const enabled = false;

const CountdownServices = {
	getCountdowns(): Promise<CountdownModel[]> {
		if (!enabled || environment === Environment.dev) {
			return Promise.resolve(countdownsMock);
		}
		const url = `${ApiUrl}${ApiEndpoint.COUNTDOWNS}`;
		const requestInit: RequestInit = {
			method: ApiMethod.GET,
		};
		return fetch(url, requestInit).then((res) => res.json());
	},
};

export default CountdownServices;
