import { Check, Clear, Remove } from '@material-ui/icons';
import React from 'react';
import './Gym.css';
import { GymState, GymWeekDayModel } from '../../models/GymModels';
import gymServices from '../../services/GymServices';
import { Scroll } from '../scroll/Scroll';

interface GymProps {
	days: GymWeekDayModel[];
	attendance: number;
	updateDays: (day: GymWeekDayModel) => void;
}

const Cell: React.FC<{ onClick?: React.MouseEventHandler }> = (props) => {
	return <div className={'gym-cell'} onClick={props.onClick}>{props.children}</div>;
};

const GymCheck = () => {
	return (
		<p className={'gym-tick'}>
			<Check />
		</p>
	);
};

const GymSkip = () => {
	return (
		<p className={'gym-cross'}>
			<Clear />
		</p>
	);
};

const GymRest = () => {
	return (
		<p className={'gym-skip'}>
			<Remove />
		</p>
	);
};

const GymDoneMarker: React.FC<{ done: GymState }> = (props) => {
	switch (props.done) {
		case GymState.done:
			return <GymCheck />;
		case GymState.skip:
			return <GymSkip />;
		case GymState.rest:
			return <GymRest />;
		default:
			return null;
	}
};

function handleClick(weekDay: GymWeekDayModel, updateDays: (weekDay: GymWeekDayModel) => void) {
	const state = gymServices.getNextGymState(weekDay.state);
	updateDays({ ...weekDay, state})
}

const days = ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'];

export const Gym: React.FC<GymProps> = (props) => {
	return (
		<Scroll>
			<div className={'gym'}>
				<div className={'gym-cells'}>
					{
						props.days.map((day, index) =>
							<Cell key={day.id} onClick={() => handleClick(day, props.updateDays)}>
								<p>{days[index]}</p>
								<GymDoneMarker done={day.state} />
							</Cell>,
						)
					}
				</div>
				{/*<p>{props.attendance}% this month.</p>*/}
			</div>
		</Scroll>
	);
};
