import React, { useEffect, useRef, useState } from 'react';
import './theme.css';
import './App.css';
import { Milestone, MilestonesContainer } from './components/milestone/Milestone';
import { Board } from './components/board/Board';
import { Gym } from './components/gym/Gym';
import { Quote } from './components/quote/Quote';
import { CountdownModel } from './models/CountdownModels';
import { MilestoneModel } from './models/MilestoneModels';
import { BoardModel } from './models/BoardModels';
import CountdownServices from './services/CountdownServices';
import MilestoneServices from './services/MilestoneServices';
import BoardServices from './services/BoardServices';
import GymServices from './services/GymServices';
import gymServices from './services/GymServices';
import { Countdowns } from './components/countdown/Countdowns';
import { Add } from '@material-ui/icons';
import { GymModel, GymWeekDayModel } from './models/GymModels';
import { debounce } from 'lodash';

function addBoard() {
	alert('board?');
}

const App: React.FC = () => {
	const [milestones, setMilestones] = useState<MilestoneModel[]>([]);
	// const [boards, setBoards] = useState<BoardModel[]>([]);
	const [gym, setGym] = useState<GymModel>({ gymWeekDays: [], gymPercent: 0 });
	const [countdowns, setCountdowns] = useState<CountdownModel[]>([]);
	const debouncedGymUpdate = useRef(
		debounce(async (gymModel: GymWeekDayModel[]) => {
			setGym(await gymServices.updateGymWeekDays(gymModel));
		}, 1000),
	).current;

	function updateGymDay(weekDay: GymWeekDayModel) {
		const updatedWeekDays = gym.gymWeekDays.map((day) => {
			if (day.id === weekDay.id) return weekDay;
			return day;
		});
		// Optimistically update state
		setGym({
			...gym,
			gymWeekDays: updatedWeekDays,
		});
		// Update API
		debouncedGymUpdate(updatedWeekDays);
	}

	// Cancel inflight requests on unmount
	useEffect(() =>
		() => {
			debouncedGymUpdate.cancel();
		}, [debouncedGymUpdate]);

	useEffect(() => {
		MilestoneServices.getMilestones().then((res) => {
			setMilestones(res);
		});

		// BoardServices.getBoards().then((res) => {
		// 	setBoards(res);
		// });

		GymServices.getGymWeekDays().then((res) => {
			setGym(res);
		});

		CountdownServices.getCountdowns().then((res) => {
			setCountdowns(res);
		});
	}, []);

	return (
		<div className='App' data-theme='dark'>
			{/*<div className='boards'>*/}
			{/*	{boards.map((board) => {*/}
			{/*		if (board.cards.length > 0)*/}
			{/*			return (*/}
			{/*				<Board*/}
			{/*					key={board.boardId}*/}
			{/*					title={board.title}*/}
			{/*					cards={board.cards}*/}
			{/*				/>*/}
			{/*			);*/}
			{/*		return null;*/}
			{/*	})}*/}
			{/*	<button className='boards-add' onClick={addBoard}><Add /></button>*/}
			{/*</div>*/}
			{/*<hr />*/}
			<div>
				<h2>Milestones</h2>
				<MilestonesContainer>
					{milestones
						.sort((a, b) => a.due.localeCompare(b.due))
						.map((milestone) => (
							<Milestone key={milestone.milestoneId} milestone={milestone} />
						))}
				</MilestonesContainer>
			</div>
			<hr />
			<div>
				<h2>Gym</h2>
				<Gym days={gym.gymWeekDays} attendance={gym.gymPercent}
						 updateDays={(days) => updateGymDay(days)} />
			</div>
			<hr />
			<Countdowns countdowns={countdowns} />
			<hr />
			<Quote />
		</div>
	);
};

export default App;
