import React from 'react';
import Moment from 'react-moment';
import { MilestoneModel } from '../../models/MilestoneModels';
import './Milestone.css';
import { Scroll } from '../scroll/Scroll';

export const MilestonesContainer: React.FC = (props) => (
	<Scroll>
		<div className={'milestones-container'}>{props.children}</div>
	</Scroll>
);

interface MilestoneProps {
	milestone: MilestoneModel;
}

export const Milestone: React.FC<MilestoneProps> = (props) => {
	return (
		<div className={'milestone'}>
			<p>{props.milestone.title}</p>
			<h4>
				<Moment date={props.milestone.due} fromNow />
			</h4>
			<p>
				<Moment date={props.milestone.due} format={'DD/MM'} />
			</p>
		</div>
	);
};
