import { gymMock, gymMock2 } from '../mocks/gymMock';
import { GymModel, GymState, GymWeekDayModel } from '../models/GymModels';
import { ApiEndpoint, ApiMethod, ApiUrl, environment, Environment } from './constants';
import ApiServices from './ApiServices';

const GymServices = {
	getGymWeekDays(): Promise<GymModel> {
		if (environment === Environment.prod) {
			return Promise.resolve(gymMock);
		}
		const url = `${ApiUrl}${ApiEndpoint.GYM}`;
		const requestInit: RequestInit = {
			method: ApiMethod.GET,
		};
		return fetch(url, requestInit).then((res) => res.json()).then(res => {
			return {
				gymWeekDays: res,
				gymPercent: 50
			}
		});
	},
	updateGymWeekDays(gymWeekDays: GymWeekDayModel[]): Promise<GymModel> {
		if (environment === Environment.prod) {
			return Promise.resolve(gymMock2);
		}
		const url = `${ApiUrl}${ApiEndpoint.GYM_WEEK}`;
		return ApiServices.postJson<GymWeekDayModel[]>(url, gymWeekDays).then(res => {
			return {
				gymWeekDays: res,
				gymPercent: 50
			}
		})
	},
	getNextGymState(state: GymState) {
		switch (state) {
			case GymState.done:
				return GymState.skip;
			case GymState.skip:
				return GymState.rest;
			case GymState.rest:
				return GymState.future;
			case GymState.future:
				return GymState.done;
		}
	}
};

export default GymServices;
