import React from 'react';
import { CountdownModel } from '../../models/CountdownModels';
import { Countdown } from './Countdown';

const today = (new Date()).toISOString();

export const Countdowns: React.FC<{ countdowns: CountdownModel[] }> = (props) => {
	return (
		<div>
			{props.countdowns
				.filter(a => a.date.localeCompare(today) >= 0)
				.sort((a, b) => a.date.localeCompare(b.date))
				.map((countdown) => (
					<Countdown key={countdown.countdownId} title={countdown.title} date={countdown.date} />
				))}
		</div>
	);
};
