const server = 'dashboard-8d69a/us-central1';

export enum Environment {
	dev,
	prod,
}

export const environment: Environment = process.env.NODE_ENV === 'production' ? Environment.prod : Environment.dev;

export const ApiUrl =
	environment === Environment.prod
		? 'https://us-central1-dashboard-8d69a.cloudfunctions.net'
		: `/${server}`;

export enum ApiEndpoint {
	COUNTDOWNS = '/countdowns',
	MILESTONES = '/milestones',
	BOARDS = '/boards',
	GYM = '/gym',
	GYM_WEEK = '/gym/week',
}

export enum ApiMethod {
	GET = 'GET',
	POST = 'POST',
	PUT = 'PUT',
	PATCH = 'PATCH',
	DELETE = 'DELETE',
}

