import React, { useEffect, useState } from 'react';
import './Quote.css';
import QuoteServices, { QuoteModel } from '../../services/QuoteServices';

export const Quote: React.FC = () => {
	const [quote, setQuote] = useState<QuoteModel>({ quote: '', author: '' });

	useEffect(() => {
		setQuote(QuoteServices.getRandomQuote())
	}, []);

	return (
		<div className={'quote'}>
			<blockquote>{quote.quote}</blockquote>
			<small>- {quote.author}</small>
		</div>
	);
};
